.acordeonvertical > ul {
    margin: 0;
    padding: 0;
    list-style: 300px;
    width: 300px;
  }
  .acordeonvertical > ul > li {
    display: block;
    overflow: hidden;
    margin: 0;
    padding: 0;
    list-style: none;
    width: 300px;
    background-color: rgb(215, 230, 245);
    border-radius: 7px;
    transition: heigh 0.3s ease-in-out;
  }
  
  .acordeonvertical > ul > li > h3 {
    display: block;
    margin: 0;
    padding: 10px;
    height: 19px;
    border-top: #f0f0f0 1px solid;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 80%;
    color: #000;
    background: rgb(185, 205, 225);
  }
  
  
